            "__v": 0,
            "type": "medical",
            "desc": "In vitro fertilisation (IVF) is a process of fertilisation where an egg is combined with sperm outside the body, in vitro (\"in glass\"). The process involves monitoring and stimulating a woman's ovulatory process, removing an ovum or ova (egg or eggs) from the woman's ovaries and letting sperm fertilise them in a liquid in a laboratory. After the fertilised egg (zygote) undergoes embryo culture for 26 days, it is implanted in the same or another woman's uterus, with the intention of establishing a successful pregnancy.",
