            "__v": 0,
            "type": "medical",
            "desc": "A depression screening, also called a depression test, helps find out if you have depression. Depression is a common, though serious, illness. Everyone feels sad at times, but depression is different than normal sadness or grief. Depression can affect how you think, feel, and behave. Depression makes it hard to function at home and work. You may lose interest in activities you once enjoyed. Some people with depression feel worthless and are at risk for harming themselves.",
