            "__v": 0,
            "desc": "Telehealth is the distribution of health-related services and information via electronic information and telecommunication technologies. It allows long-distance patient and clinician contact, care, advice, reminders, education, intervention, monitoring, and remote admissions. The following conditions are relevant for telehealth: \n- Cough\n- Sinus infection \n- Sore throat \n- Pinkeye\n- Cold \n- Flu \n- Depression \n- Anxiety \n- In-home medication management (local providers only) \n- Post-surgical follow ups (local providers only)",
