            "desc": "Abdominal aortic aneurysm (AAA or triple A) is a localized enlargement of the abdominal aorta such that the diameter is greater than 3 cm or more than 50% larger than normal. They usually cause no symptoms, except during rupture. Occasionally, abdominal, back, or leg pain may occur. Large aneurysms can sometimes be felt by pushing on the abdomen. Rupture may result in pain in the abdomen or back, low blood pressure, or loss of consciousness, and often results in death.",
